// Sim.js
import React from "react";
import Card from "../component/Card";
import data from "../data";
import Detail from "../page/Detail";

const Sim = () => {
  const ballItems = data.filter((item) => item.name === "심지수");

  return (
    <div className="ball">
      <h2>심지수</h2>
      {ballItems.map((item) => (
        <Card item={item} />
      ))}
    </div>
  );
};

export default Sim;
