import React from "react";
import Card from "../component/Card";
import data from "../data";
import Detail from "../page/Detail";

const Sunchel = () => {
  const ballItems = data.filter((item) => item.name === "이선철");
  return (
    <div className="ball">
      <h2>이선철</h2>
      {ballItems.map((item) => (
        <Card item={item} />
      ))}
    </div>
  );
};

export default Sunchel;
