import React from "react";
import Card from "../component/Card";
import data from "../data";
import Detail from "../page/Detail";

const CarafeBall = () => {
  const ballItems = data.filter((item) => item.list === "주병");

  return (
    <div className="ball">
      <h2>주병</h2>
      {ballItems.map((item) => (
        <Card item={item} />
      ))}
    </div>
  );
};

export default CarafeBall;
