import React from "react";
import Card from "../component/Card";
import data from "../data";
import Detail from "../page/Detail";

const Ball = () => {
  const ballItems = data.filter((item) => item.list === "볼");

  return (
    <div className="ball">
      <h2>Ball</h2>
      {ballItems.map((item) => (
        <Card item={item} />
      ))}
    </div>
  );
};

export default Ball;
