import React from "react";
import Card from "../component/Card";
import data from "../data";
import Detail from "../page/Detail";

const Sangouk = () => {
  const ballItems = data.filter((item) => item.name === "이상욱");
  return (
    <div className="ball">
      <h2>이상욱</h2>
      {ballItems.map((item) => (
        <Card item={item} />
      ))}
    </div>
  );
};

export default Sangouk;
