import React from "react";
import Card from "../component/Card";
import data from "../data";
import Detail from "../page/Detail";

const Cup = () => {
  const ballItems = data.filter((item) => item.list === "컵");
  return (
    <div className="ball">
      <h2>컵</h2>
      {ballItems.map((item) => (
        <Card item={item} />
      ))}
    </div>
  );
};

export default Cup;
