import React from "react";
import Card from "../component/Card";
import data from "../data";
import Detail from "../page/Detail";

const Dish = () => {
  const ballItems = data.filter((item) => item.list === "찬기");
  return (
    <div className="ball">
      <h2>찬기,종지</h2>
      {ballItems.map((item) => (
        <Card item={item} />
      ))}
    </div>
  );
};

export default Dish;
