import React from "react";
import Card from "../component/Card";
import data from "../data";
import Detail from "../page/Detail";

const Kungran = () => {
  const ballItems = data.filter((item) => item.name === "여경란");
  return (
    <div className="ball">
      <h2>여경란</h2>
      {ballItems.map((item) => (
        <Card item={item} />
      ))}
    </div>
  );
};

export default Kungran;
