import React from "react";
import Card from "../component/Card";
import data from "../data";
import Detail from "../page/Detail";

const Plate = () => {
  const ballItems = data.filter((item) => item.list === "접시");
  return (
    <div className="ball">
      <h2>접시</h2>
      {ballItems.map((item) => (
        <Card item={item} />
      ))}
    </div>
  );
};

export default Plate;
