import React from "react";
import Card from "../component/Card";
import data from "../data";
import Detail from "../page/Detail";

const Sangman = () => {
  const ballItems = data.filter((item) => item.name === "김상만");
  return (
    <div className="ball">
      <h2>김상만</h2>
      {ballItems.map((item) => (
        <Card item={item} />
      ))}
    </div>
  );
};

export default Sangman;
