import React from "react";
import Card from "../component/Card";
import data from "../data";
import Detail from "../page/Detail";

const Dripper = () => {
  const ballItems = data.filter((item) => item.list === "드리퍼");
  return (
    <div className="ball">
      <h2>드리퍼</h2>
      {ballItems.map((item) => (
        <Card item={item} />
      ))}
    </div>
  );
};

export default Dripper;
