import React from "react";
import Card from "../component/Card";
import data from "../data";
import Detail from "../page/Detail";

const Kang = () => {
  const ballItems = data.filter((item) => item.name === "강유단");
  return (
    <div className="ball">
      <h2>강유단</h2>
      {ballItems.map((item) => (
        <Card item={item} />
      ))}
    </div>
  );
};

export default Kang;
