import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, Pagination, Navigation } from "swiper/modules";
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";

const Visual = () => {
  return (
    <div>
      <Swiper
        spaceBetween={30}
        centeredSlides={true}
        autoplay={{
          delay: 2500,
          disableOnInteraction: false,
        }}
        pagination={{
          clickable: true,
        }}
        navigation={true}
        modules={[Autoplay, Pagination, Navigation]}
        className="mySwiper"
      >
        <SwiperSlide className="visual1">
          <div className="text1">
            <h2>영원한 품격</h2>
            <p>
              감각적인 디자인에 굳건한 장인정신을 더해 빚어내는
              <br />
              아트 세라믹, 삶의 새로운 장면을 열어줍니다.
            </p>
          </div>
          <div className="name1">
            <h2>Pottery Damdam</h2>
            <p>
              [ 김상만 ]
              <br />
              '자연'을 닮은 그릇
            </p>
          </div>
        </SwiperSlide>
        <SwiperSlide className="visual2">
          <div className="text2">
            <h2>수공예 도자의 특별한 가치</h2>
            <p>
              전통 제작 방식과 전문화된 기술을 기반으로, 현대적인 감각을 더해
              디자인합니다.
              <br />
              한국 도자의 전통을 창조적으로 계승해 장인의 혼을 담은 이도의
              제품은 일상을 특별하게 만들어줍니다.
            </p>
          </div>
          <div className="name">
            <h2>Pottery Yuyu</h2>
            <p>
              [ 강유단 ]
              <br />
              화려함을 덮은'흑유'
            </p>
          </div>
        </SwiperSlide>
        <SwiperSlide className="visual3">
          <div className="text3">
            <h2>흉내낼 수 없는 일상의 아름다움</h2>
            <p>
              고유의 디자인 모티브와 재료에 대한 심층적인 이해를 바탕으로
              <br />
              조형적 균형미를 일궈냅니다.
            </p>
          </div>
          <div className="name">
            <h2>Shim's Ceramic Studio</h2>
            <p>
              [ 심지수 ]
              <br />
              기능을 수반하는'아름다움'
            </p>
          </div>
        </SwiperSlide>
        <SwiperSlide className="visual4">
          <div className="text4">
            <h2>
              늘해랑만이 그려낼 수 있는
              <br />
              독창적인 품격
            </h2>
            <p>
              새로운 차원의 프리미엄을 제안하는
              <br />
              아트 라이프스타일 리딩 브랜드
            </p>
          </div>
          <div className="name">
            <h2>Pottery Nokdu</h2>
            <p>
              [ 이상욱 ]
              <br />
              '담음새'가 좋은 그릇
            </p>
          </div>
        </SwiperSlide>
        <SwiperSlide className="visual5">
          <div className="text5">
            <h2>공간을 완성하는 프리미엄 비스포크 아트 세라믹</h2>
            <p>
              오랜 시간 축적한 고도의 세라믹 기술을 바탕으로 건축 및 인테리어
              외장재를 만듭니다.
              <br />
              장인의 기술력과 예술적 조형감을 갖춘 아트 세라믹 공간의 품격을
              높여줍니다.
            </p>
          </div>
          <div className="name">
            <h2>Pottery Gonggi</h2>
            <p>
              [ 이선철 ]
              <br />
              작지만 완벽한 무엇'공기'
            </p>
          </div>
        </SwiperSlide>
      </Swiper>
    </div>
  );
};

export default Visual;
