import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";
import {
  Navbar,
  Nav,
  Container,
  NavDropdown,
  Form,
  Button,
} from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faMagnifyingGlass } from "@fortawesome/free-solid-svg-icons";
import { Routes, Route, Link, Navigate } from "react-router-dom";
import Home from "./page/Home";
import Visual from "./Visual";
import Footer from "./page/Footer";
import { useState } from "react";
import data from "./data";

// page
import Ball from "./page/Ball";
import CarafeBall from "./page/CarafeBall";
import Cup from "./page/Cup";
import Detail from "./page/Detail";
import Dish from "./page/Dish";
import Dripper from "./page/Dripper";
import Plate from "./page/Plate";
import Event from "./page/Event";
import Event1 from "./page/Event1";
import Event2 from "./page/Event2";
// 작가
import Sim from "./people/Sim";
import Kang from "./people/Kang";
import Kungran from "./people/Kungran";
import Sangman from "./people/Sangman";
import Sangouk from "./people/Sangouk";
import Sunchel from "./people/Sunchel";

function App() {
  const [query, setQuery] = useState("");
  const [search, setSearch] = useState("");

  const searchChange = (e) => {
    setQuery(e.target.value);
  };

  const searchKey = (e) => {
    if (e.key === "Enter") {
      setSearch(query);
    }
  };

  const searchButton = (e) => {
    e.preventDefault();
    setSearch(query);
  };

  const filterData = data.filter((item) => item.title.includes(search));
  return (
    <>
      <Navbar expand="lg" className="bg-body-tertiary">
        <Container fluid>
          <Navbar.Toggle aria-controls="navbarScroll" />
          <Navbar.Collapse id="navbarScroll">
            <Nav
              className="me-auto my-2 my-lg-0"
              style={{ maxHeight: "100px" }}
              navbarScroll
            >
              <NavDropdown title="그릇" id="basic-nav-dropdown">
                <NavDropdown.Item as={Link} to="/Plate">
                  접시
                </NavDropdown.Item>
                <NavDropdown.Item as={Link} to="/Dish">
                  찬기
                </NavDropdown.Item>
                <NavDropdown.Item as={Link} to="/Ball">
                  볼
                </NavDropdown.Item>
              </NavDropdown>
              <NavDropdown title="카페" id="basic-nav-dropdown">
                <NavDropdown.Item as={Link} to="/Cup">
                  컵
                </NavDropdown.Item>
                <NavDropdown.Item as={Link} to="/Dripper">
                  드리퍼,저그
                </NavDropdown.Item>
                <NavDropdown.Item as={Link} to="/CarafeBall">
                  다기,주병
                </NavDropdown.Item>
              </NavDropdown>
              <NavDropdown title="작가" id="basic-nav-dropdown">
                <NavDropdown.Item as={Link} to="/Kang">
                  강유단
                </NavDropdown.Item>
                <NavDropdown.Item as={Link} to="/Sim">
                  심지수
                </NavDropdown.Item>
                <NavDropdown.Item as={Link} to="/Sangouk">
                  이상욱
                </NavDropdown.Item>
                <NavDropdown.Item as={Link} to="/Sangman">
                  김상만
                </NavDropdown.Item>
                <NavDropdown.Item as={Link} to="/Kungran">
                  여경란
                </NavDropdown.Item>
                <NavDropdown.Item as={Link} to="/Sunchel">
                  이선철
                </NavDropdown.Item>
              </NavDropdown>
              <Nav.Link as={Link} to="/Event">
                이벤트
              </Nav.Link>
            </Nav>
            <Nav.Link className="title" as={Link} to="/"></Nav.Link>
            <Form className="d-flex" onSubmit={searchButton}>
              <Form.Control
                type="search"
                placeholder="검색"
                className="me-2"
                aria-label="Search"
                onChange={searchChange}
                onKeyDown={searchKey}
              />
              <Button variant="outline" onClick={searchButton}>
                <FontAwesomeIcon className="search" icon={faMagnifyingGlass} />
              </Button>
            </Form>
          </Navbar.Collapse>
        </Container>
      </Navbar>
      <Routes>
        <Route
          path="/"
          element={
            <>
              <Visual />
              <Home
                searchData={filterData} // 검색 결과를 Home 컴포넌트에 전달
                onSearchChange={searchChange} // 검색 변경 핸들러 전달
                onSearchKeyDown={searchKey} // 검색 키 다운 핸들러 전달
                onSearchButtonClick={searchButton} // 검색 버튼 클릭 핸들러 전달
              />
            </>
          }
        />
        <Route path="/Ball" element={<Ball />} />
        <Route path="/CarafeBall" element={<CarafeBall />} />
        <Route path="/Cup" element={<Cup />} />
        <Route path="/Dish" element={<Dish />} />
        <Route path="/Dripper" element={<Dripper />} />
        <Route path="/Plate" element={<Plate />} />
        <Route path="/Event" element={<Event />}>
          <Route path="Event1" element={<Event1 />} />
          <Route path="Event2" element={<Event2 />} />
          <Route path="/Event" element={<Navigate to="Event1" />} />
        </Route>
        <Route path="/Sim" element={<Sim />} />
        <Route path="/Kang" element={<Kang />} />
        <Route path="/Kungran" element={<Kungran />} />
        <Route path="/Sangman" element={<Sangman />} />
        <Route path="/Sangouk" element={<Sangouk />} />
        <Route path="/Sunchel" element={<Sunchel />} />
        <Route path="/product/:id" element={<Detail data={data} />} />
      </Routes>
      <Footer />
    </>
  );
}

export default App;
